html { font-size: 62.5%; } 
html, body {
  width: 100%;
  height: 100%; }

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-font-smoothing: antialiased; }

table {
  border-collapse: collapse;
  width: 100%; }

input:focus {
  outline: none; }

img, table {
  border: none; }

a {
  color: #4176FA;
  text-decoration: none;
  transition: all 0.5s ease; 
}


button {
  outline: none;
  cursor: pointer; }

svg:not(:root) {
  overflow: hidden; }

header, footer, article, section, hgroup, nav, menu, aside, figure, main, video, address, picture, figcaption {
  display: block; }

h1, h2, h3, h4, h5, h6 {
  margin: 1rem 0;
  }

h1 {
  font-size: 8.8rem; }

h2 {
  font-size: 6rem; }

h3 {
  font-size: 3.6rem; }

h4 {
  font-size: 1.8rem; }

h5 {
  font-size: 1.6rem; }

h6 {
  font-size: 1.4rem; }

ul, ol {
  list-style: none; }

p {
  margin: 1rem 0; }

img {
